import Vue from 'vue';
// 
import Confirm from './Confirm.vue';
const noop = () => { };
//  3,    
function showConfirm({ title, cancel, desc, confirm, cancelTxt, confirmTxt }) {
    const div = document.createElement('div');
    const el = document.createElement('div');
    div.appendChild(el);
    document.body.appendChild(div);
    const confirmDialogProps = {
        props: {
            visible: true,
            title: title
        }
    };
    const props = {};
    new Vue({
        el: el,
        data() {
            return {
                title: title,
                desc: desc,
                visible: false,
                confirmTxt: confirmTxt,
                cancelTxt: cancelTxt
            };
        },
        mounted() {
            this.visible = true;
            this.title = title;
            this.desc = desc;
        },
        render(createElement) {
            return createElement(Confirm, {
                props: {
                    title: this.title,
                    desc: this.desc,
                    visible: this.visible,
                    confirmTxt: this.confirmTxt,
                    cancelTxt: this.cancelTxt
                },
                on: {
                    cancel: cancel ? cancel : noop,
                    confirm: confirm ? confirm : noop,
                    'update:title': (title) => {
                        this.title = title;
                    }
                }
            });
        }
    });
}
